.App {
  text-align: center;
  min-height: 100vh;
  background: none;
  cursor: url('data:image/x-icon;base64,'), auto;
}

#grid-canvas {
  background-color: black;
}

.rfm-marquee-container {
  background: black;
  color: white;
  position: absolute !important;
  top: 65px;
  height: 167px;
  width: 232px !important;
  border-radius: 8px;
  left: 50%;
  transform: translateX(-50%) !important;
  z-index: 10000;
  font-size: 24px;
  transition: all 0.4s ease-out;
}

.on.rfm-marquee-container {
  background: white;
  color: black;
}


.monitor-wrapper {
  position: relative;
  width: fit-content;
  margin: 0 auto;
}

.theme-button {
  position: absolute;
  bottom: 83px;
  right: 43px;
  width: 33px;
  height: 23px;
  cursor: pointer;
}

.timer-text {
  font-size: 30px;
  display: block;
  padding: 0 20px;
  font-family: "Press Start 2P", system-ui;
  font-weight: 400;
  font-style: normal;
}

.text-block {
  max-width: 500px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
  color: white;
  padding: 10px 20px;
  line-height: 1.6
}

.text-block h3 {
  font-family: "Press Start 2P", system-ui;
  font-size: 20px;
  margin: 0;
  text-align: right;
}

.text-block p {
  font-family: "Tektur", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  text-align: justify;
  word-break: break-word;
}

.redacted {
  background: white;
}

.main-title {
  font-family: "Tektur", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  word-break: break-word;
  color: white;
}

@media (max-width: 900px) {
  .App {
    background: black;
    overflow-x: hidden;
  }

  #grid-canvas {
    display: none;
  }
}

.game,
.rps-game {
  z-index: 12121212;
  position: absolute;
  background: white;
  border-radius: 5px;
  width: 233px;
  height: 167px;
  top: 65px;
  left: 71px;
  overflow: hidden;
}

.rps-game {
  font-family: "Press Start 2P", system-ui;
}

.rps-game .title {
  font-size: 13px;
  margin-top: 15px;
}

.options {
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  align-items: center;
  margin-top: 35px;
}

.options>div {
  width: 50%
}

.rps-game .result {
  display: flex;
  justify-content: center;
  gap: 60px;
  margin-top: 30px;
  align-items: center;
  padding: 0 30px;
}

.rps-game.lose {
  background: #c8c8c8;
}

.result p {
  margin: 0 0 10px;
}

.spinner {
  position: absolute;
  top: 80px;
  left: 90px;
}

.tie-img {
  width: 50px;
  position: absolute;
  top: 35px;
  left: 90px;
  animation: vertical-shaking 0.7s infinite;
}

.win-img {
  width: 413px;
  position: absolute;
  top: -25px;
  left: -96px;
  z-index: -1;
}

.lose-button {
  width: 78px;
  position: absolute;
  top: 47px;
  left: 75px;
  outline: none;
  border: 2px solid red;
  font-family: "Press Start 2P", system-ui;
  background-color: white;
  color: red;
  padding: 10px;
  font-size: 9px;
  margin-top: 20px;
  cursor: pointer;
}

.game .numbers {
  width: 100%;
  position: absolute;
  color: black;
  font-size: 28px;
  font-family: "Press Start 2P", system-ui;
  text-align: center;
  pointer-events: none;
}

.game .numbers.score {
  top: 10px;
}

.game .numbers.timer {
  bottom: 10px;
}

.game .ball {
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  background: black;
  position: absolute;
  left: 38.5%;
  top: 35%;
  box-sizing: border-box;
  border: 1px solid white;
  transition: .2s ease;
}

.game .ball.end {
  pointer-events: none;
}

.game .final {
  margin-top: 10px;
  color: black;
  font-size: 20px;
  line-height: 1.8;
  font-family: "Press Start 2P", system-ui;
}

.game .button {
  outline: none;
  border: 2px solid black;
  font-family: "Press Start 2P", system-ui;
  background-color: white;
  color: black;
  padding: 10px;
  font-size: 15px;
  margin-top: 20px;
  cursor: pointer;
}

@keyframes vertical-shaking {
  0% {
    transform: translateY(0)
  }

  25% {
    transform: translateY(5px)
  }

  50% {
    transform: translateY(-5px)
  }

  75% {
    transform: translateY(5px)
  }

  100% {
    transform: translateY(0)
  }
}